<template>
  <div class="p-2">
    <form @submit.prevent="add()">
      <div class="form-group">
        <input
          v-model="message.name"
          type="text"
          name="name"
          class="form-control text-white bg-dark border-0 mt-3"
          placeholder="n a m e"
          v-focus
        />
        <i
          class="material-icons text-info mr-2"
          style="float: right;position: relative;top:-28px;"
        >person</i>
      </div>
      <div class="form-group">
        <input
          v-model="message.phone"
          type="number"
          name="phone"
          class="form-control bg-dark border-0 text-white"
          placeholder="p h o n e"
        />
        <i
          class="material-icons text-info mr-2 pointer"
          style="float: right;position: relative;top:-28px;"
        >phone</i>
      </div>
      <div class="form-group">
        <i class="mr-1 text-secondary" style="float: left;">required *</i>
        <textarea
          v-model="message.msg"
          rows="3"
          name="text"
          class="form-control bg-dark border-0 text-white"
          placeholder="message"
        ></textarea>
      </div>
      <input
        class="contact-send mt-3 btn pl-4 pr-4 btn-info text-dark"
        type="submit"
        for="customFile"
        value="Send"
      />
    </form>
  </div>
</template>
<script>
export default {
  data: function() {
    return {
      message: {
        name: "",
        phone: "",
        msg: ""
      }
    };
  },
  methods: {
    add() {
      this.message.msg.length < 1
        ? alert("message empty")
        : this.$emit("input", this.message);
      this.message.name = '',
      this.message.phone = '',
      this.message.msg = ''        
    }
  },
};
</script>