<template>
  <div class="p-md-5 p-2">
    <div class="row no-gutters">
      <div class="col-md-8 dark-box-shadow box offset-md-2 mt-3">
        <!-- notify send message status -->
        <p
          v-if="this.$store.getters.getSendMessageStatus.length > 0"
          class="notify dark-box-shadow p-2 text-white"
        >
          {{ this.$store.getters.getSendMessageStatus }}
          <button
            @click="updateStateNotifyMsgToUser"
            class="material-icons float-right ml-3 pointer close"
          >
            cancel
          </button>
        </p>

        <!-- go home btn -->
        <router-link to="/">
          <button class="btn btn-sm bg-dark pl-3 pr-3 float-right text-white rounded">
            X
          </button>
        </router-link>

        <!-- form title -->
        <h6 class="mt-5 pb-3 text-center text-info">HAVE SOME QUESTIONS ?</h6>

        <div class="row no-gutters p-md-3 rounded">
          <!-- user input -->
          <div class="col-md-6">
            <ContactForm @input="sendMsg" />
          </div>

          <!-- my info -->
          <div class="col-md-6 p-md-3 mb-5 mt-md-0 mt-3">
            <ul class="pl-3 mt-1 ml-md-3" style="list-style: none">
              <li class="text-info">
                <span class="material-icons float-left">phone</span>
                <a
                  class="text-info ml-4"
                  href="tel:2222"
                  style="text-decoration: none"
                  >Unavailable</a
                >
              </li>
              <li class="mt-3 text-info">
                <span class="material-icons float-left">mark_email_unread</span>
                <a
                  class="text-info ml-4"
                  href="mailto:2222@maloe.com"
                  style="text-decoration: none"
                  >Unavailable</a
                >
              </li>
              <li class="mt-3 text-info">
                <span class="material-icons float-left">location_on</span>

                <a href class="ml-4 text-info" style="text-decoration: none"
                  >Myanmar/Mandalay/Kyaukpadaung</a
                >
              </li>
            </ul>
            <!--social link-->
            <div class="ml-5 mt-md-5">
              <a href="https://www.facebook.com/shineshine2222">
                <img
                  src="../assets/imgs/brands/facebook.svg"
                  class="ml-1 rotate icon-border-yel"
                  width="30px"
                />
              </a>
              <a href="https://twitter.com">
                <img
                  src="../assets/imgs/brands/twitter.svg"
                  class="ml-2 rotate icon-border-yel"
                  width="30px"
              /></a>
              <a href="https://github.com/ShineShineDev"
                ><img
                  src="../assets/imgs/brands/github.svg"
                  class="ml-2 rotate icon-border-yel"
                  width="30px"
              /></a>
            </div>
          </div>
        </div>
      </div>

      <!-- send message -->
      <div class="col-md-8 offset-md-2 mt-3">
        <h4 class="text-center text-white border-bottom">Your Message</h4>
        <div class="card-columns">
          <div
            v-for="(item, index) in getMsg"
            :key="index"
            class="card border-0 p-2 box"
            style="background: rgba(255, 255, 255, 0.1)"
          >
            <div class="card-body p-1">
              <p class="text-info" style="white-space: pre-line">{{ item.msg }}</p>
              <small class="text-info float-right">{{ item.date.slice(4) }}</small>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ContactForm from "./../components/ContactForm.vue";

export default {
  created() {
    this.$store.dispatch("initMsg");
  },
  computed: {
    getMsg() {
      return this.$store.getters.getMessage;
    },
  },
  methods: {
    updateStateNotifyMsgToUser() {
      this.$store.dispatch("updateStateNotifyMsgToUser");
    },

    sendMsg: function (event) {
      var msg_schema = {
        name: event.name,
        phone: event.phone,
        msg: event.msg,
        date: new Date().toDateString(),
      };
      this.$store.dispatch("sendMsg", msg_schema);
    },
  },
  components: { ContactForm },
};
</script>
