export default {
    state: {
        notify_msg_to_user: '',
        message: []
    },
    getters: {
        getMessage: state => state.message,
        getSendMessageStatus: state => state.notify_msg_to_user
    },
    actions: {
        initMsg(context) {
            context.commit("INITMSG")
        },
        updateStateNotifyMsgToUser(context){
            context.commit("UPDATESTATENOTIFYMSGTOUSER")
        },
        sendMsg(context, msg_schema) {
            console.log(context)
            //call api
            var api = true
            api ? 
            context.state.notify_msg_to_user = "Successfully Send Message" :
            context.state.notify_msg_to_user = "Fail Send Message" ;

            context.commit("ADDMSGTOSTORAGE", msg_schema)
        }
    },
    mutations: {
        INITMSG(state) {
            // localStorage.clear()
            state.message = JSON.parse(localStorage.getItem('shine')) || []
        },
        ADDMSGTOSTORAGE(state, msg_schema) {
            state.message.push(msg_schema)
            localStorage.setItem('shine', JSON.stringify(state.message))
        },
        UPDATESTATENOTIFYMSGTOUSER(state){
            state.notify_msg_to_user = ''
        }
    }
}